@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: 'Roboto';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(100deg, #cfd9df 0%, #e2ebf0 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.Navbar-logo {
  width: 30px;
  height: 30px;
  padding-bottom: 4px;
}

.Navbar-title {
  display: inline-block;
  margin-left: 10px;
}

.Navbar {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.015), 0 10px 7px rgba(0, 0, 0, 0.01);
}

.Login {
  margin-top: 5rem;
  width: 350px;
  -webkit-animation: 0.3s ease-in 0s 1 appear;
          animation: 0.3s ease-in 0s 1 appear;
}

@-webkit-keyframes appear {
  0% {
    opacity: 0%;
  }
  100% {
    opacity: 100%;
  }
}

@keyframes appear {
  0% {
    opacity: 0%;
  }
  100% {
    opacity: 100%;
  }
}

.Signup {
  text-decoration: none;
  color: black;
}

.Spinner {
  width: 25px;
  height: 25px;
}

.HomePage-card {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.015), 0 10px 7px rgba(0, 0, 0, 0.01);
  -webkit-animation: 0.3s ease-in 0s 1 appear;
          animation: 0.3s ease-in 0s 1 appear;
}

@-webkit-keyframes appear {
  0% {
    opacity: 0%;
  }
  100% {
    opacity: 100%;
  }
}

@keyframes appear {
  0% {
    opacity: 0%;
  }
  100% {
    opacity: 100%;
  }
}

.Stopwatch-timer {
  display: flex;
  justify-content: center;
  margin-bottom: 1rem;
}

.Stopwatch-timer h4 {
  font-size: 120%;
}

.Stopwatch-controls button {
  width: 45%;
}

.Stopwatch-controls {
  display: flex;
  justify-content: space-around;
}

.Stopwatch-submit button {
  margin-top: 0.5rem;
  width: 95%;
}

.Stopwatch-submit {
  display: flex;
  justify-content: space-around;
  transition: all 2s linear;
  -webkit-animation: 0.5s ease-out 0s 1 slideIn;
          animation: 0.5s ease-out 0s 1 slideIn;
}

@-webkit-keyframes slideIn {
  0% {
    -webkit-transform: translateY(-20%);
            transform: translateY(-20%);
    opacity: 0%;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 100%;
  }
}

@keyframes slideIn {
  0% {
    -webkit-transform: translateY(-20%);
            transform: translateY(-20%);
    opacity: 0%;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 100%;
  }
}

.HistoryItem {
  display: flex;
  padding: 0.8rem;
}

.History-line {
  display: flex;
  border-bottom: 1px solid lightgrey;
}

